#root,
.App {
  min-width: 100%;
  min-height: 100%;
}

.title {
  text-align: center;
  margin-bottom: 20px;
}

.demo-container {
  position: fixed;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.column-left {
  width: 30%;
  display: flex;
  flex-direction: column;
  background: #f7fafc;
  border-right: 1px solid #2d3748;
  z-index: 2;
}

.controls-container {
  display: flex;
  flex-direction: column;
  flex-basis: 1;
  overflow-y: auto;
  margin: 4px;
}

.state-container {
  display: none;
  flex-direction: column;
  flex-basis: 30%;
  border-top: 1px solid black;
}

.state-container > h4 {
  color: black;
}

.state {
  width: 100%;
  height: 100%;
}

.column-right {
  height: 100%;
  width: 100%;
}

.tree-background {
  height: 100%;
  background-image: url("assets/family_tree.jpg");
  filter: blur(4px);
  -webkit-filter: blur(4px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
}

.tree-container {
  height: 100%;
  width: 100%;
  padding: 16px;
  /*background: #ccf6c8;*/
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.prop-container {
  padding: 2px 5px;
  margin: 15px 10px;
}

.prop {
  font-weight: bold;
  display: block;
}

.prop-large {
  font-size: 16px;
}

.sub-prop {
  padding: 5px;
}

.btn-controls {
  color: #f7fafc;
  background-color: #2d3748;
  transition: color 0.15s ease, border-color 0.15s ease;
}

.btn-controls:hover,
.btn-controls:focus,
.btn-controls:active {
  color: white;
  border-color: #f7fafc;
}

.header-container {
  position: fixed;
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 0.5rem 2rem;
  border-bottom: 1px solid #2d3748;
}

.tree-stats-container {
  text-align: center;
  font-weight: bold;
}

/* Custom node classes */
.demo-node > circle {
  fill: #2d3748;
  stroke: #2d3748;
}

/* Custom path classes */
.my-path-class {
  stroke: royalblue;
  stroke-width: 10;
}

.flex-container-vertical {
  display: flex;
  flex-flow: column;
}

.flex-conservative {
  flex: 0 1 auto;
}

.flex-greedy {
  flex: 1 1 auto;
}
